import { promises as fs } from 'fs';
import path from 'path';
import { SentenceData } from '@/types';
import TranscriptList from '@/components/TranscriptList';

export default async function Home() {
    const filePath = path.join(process.cwd(), 'data', 'annotated_transcript.json');
    const fileContents = await fs.readFile(filePath, 'utf8');
    const data: SentenceData[] = JSON.parse(fileContents);

    return (
        <main className="min-h-screen bg-[#FAFAF9] text-slate-900 font-sans selection:bg-yellow-200 selection:text-yellow-900">
            <div className="absolute top-0 left-0 w-full h-96 bg-gradient-to-b from-yellow-50/80 to-transparent pointer-events-none" />
            <TranscriptList data={data} />
        </main>
    );
}
